<template>
  <transition name="fade" mode="in-out">
    <div class='forumDetailsPage'>
      <v-Header class="head">
        <span slot='text'>社区详情</span>
      </v-Header>
      <div id='forumDetailsScroll' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <div>
          <v-forumList :forumLists='forumLists' :removePost='removePost' :sendComment='Comment' :isforumPage=false />
          <v-loadmove v-if='forumLists.length>0&&forumLists[0].commentList.length>0' :type='loadType' style="margin-top:-0.3rem"/>
        </div>
      </div>
      <mt-popup class='addCommentpopup' :modal=false v-model="addCommentVisible" position="right">
        <v-commentpage :isforumPage=true :articleId='sendCommentId' :atUserId='sendCommentatUserId' :comeBack='Back' v-on:update-comment-list="initPage"/>
      </mt-popup>
    </div>
  </transition>
</template>
<script>
import { MessageBox,Indicator } from 'mint-ui';
import Header from '../../components/header';
import forumList from '../forum/forumList';
import commentPage from '../articleDetails/commentPage';
import { mapState } from 'vuex';
import loadMove1 from '../../components/seeOcean/loadMore';
export default {
    name:'',
    props:['sendComment','needUpdate'],
    components:{
      'v-Header':Header,
      'v-forumList':forumList,
      'v-commentpage': commentPage,
      'v-loadmove': loadMove1,
    },
    data () {
        return {
          discussId:this.$route.params.discussId,
          forumLists:[],
          addCommentVisible:false,
          sendCommentId:0,
          sendCommentatUserId:0,
          loadType:'more',
          loading:true,
          lastCommentId:0,
        }
    },
    computed:{
      ...mapState({
        _userid:state => state.userInfo.userId,//当前用户id
      }),
    },
    watch:{
      needUpdate(){
        this.initPage();
      }
    },
    methods:{
      loadMore(){
        this.getComment();
      },
       //获取文章评论
      getComment(){
        if(this.loadType == 'all'||this.loading) return;
        this.loading = true;
        this.$http.get(this.$port.COMMENT_LIST_PROT,{
          params:{
            contId:this.discussId,
            lastCommentId:this.lastCommentId
          }
        }).then((response) => {
          const {data} = response;
          this.loading = false;
          if(response.code == 200){           
            this.forumLists[0].commentList=[...this.forumLists[0].commentList,...data];
            if(data.length>0){
              this.lastCommentId=data.pop().commentId;
              this.loadType = 'more';
            }else{
              this.loadType = 'all';
            }
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response.data);
        });
      },
      Comment(discussId,atUserId=0,commentId=0){
        if(atUserId==this._userid){
          this.sendComment(discussId,atUserId,commentId)
        }else{
          this.sendCommentId=discussId;
          this.sendCommentatUserId=atUserId;
          this.addCommentVisible = true;
        }
      },
      initPage(){
        this.getDiscussList();
        this.$emit('update-comment-list');
      },
      getDiscussList(){
        this.$http.get(`${this.$port.DISCUSS_DETAILS_PROT}?discussId=${this.discussId}`,{
				}).then((response) => {
          const {data} = response;
					if(response.code == 200){
            this.forumLists = [data];
            console.log(data.commentList)
            if(data.commentList.length<30){
              this.loadType = 'all';
              this.loading = true;
            }else{
              this.loading = false;
            }
            if(data.commentList.length>0){
              this.lastCommentId=[...data.commentList].pop().commentId;
            }else{
              this.loadType = 'all';
              this.loading = true;
            }
            console.log(this.forumLists)
					}
				})
				.catch((error) => {
					console.log(error.response.data);
				});
      },
      removePost(discussId){
        event.stopPropagation();
        MessageBox({
          title: '',
          message: '确定删除吗?',
          showCancelButton: true,
          confirmButtonText:'删除'
        }).then(action => {
          if(action==='confirm'){
            Indicator.open({
              text: '正在删除...',
            });
            this.$http.post(`${this.$port.DISCUSS_DEL_PROT}?discussId=${discussId}`,{
            }).then((response) => {
              const {data} = response;
              if(response.code == 200){
                Indicator.close();
                this.$emit('update-comment-list');
                this.$router.go(-1);
              }
            })
            .catch((error) => {
              console.log(error.response.data);
            });
          }
        });
      },
      Back(){
         this.addCommentVisible = false;
      },
    },
    created() {
      this.getDiscussList();
    },
}
</script>

<style lang="scss" scoped>
*{
  border: none;
  margin: 0;
  padding: 0;
}
.forumDetailsPage{
  position: absolute;
  top: 0;
  left: 0;
  width:100%;
  height:100%;
  background:white;
  overflow:hidden;
  #forumDetailsScroll{
    position: absolute;
    top: 0.82rem;
    left: 0;
    right:0;
    bottom:0;
    margin:auto;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
  hr{
    width:100%;
    height: 0.02rem;
    background: #f2f2f2;
    }
      .addCommentpopup{
      width: 100%;
      height:100%;
      background:rgba(0,0,0,0);
    }
}
</style>




